import React from "react";
import logo from "../../img/logo.png";
import userAva from "../../img/user-ava.jpg";
import "./style.css";

export default function HeadComponent(props) {
  return (
    <div className="headComponent">
      <div className="label">
        <img src={logo} alt="Logo" className="logo" />
      </div>
      <div className="padding-bottom-center" />
      <div className="infoUser">
        <img src={userAva} alt="Logo" className="user-ava" />
        <div className="padding-bottom-right" />
        <span className="user-name">Одри Хепберн</span>
      </div>
    </div>
  );
}
