import React, { useState } from "react";
import { ButtonViewContainer } from "../../containers/ButtonViewContainer";
import MenuCreateComponent from "../MenuCreateComponent";

import "./style.css";

export default function BlockCreateComponent(props) {
  const { title, lastIdBlocks, handleChangeAddBlock } = props;
  const [isOpen, setIsOpen] = useState(false);
  const [isHover, setIsHover] = useState(false);

  return (
    <div className="container">
      <div
        className={`block block-create`}
        onMouseEnter={() => setIsHover(true)}
        onMouseLeave={() => setIsHover(false)}
      >
        {!isOpen && <span className="span-text span-text-blue">{title}</span>}

        {isHover && (
          <div>
            {isOpen ? (
              <MenuCreateComponent
                setIsOpen={setIsOpen}
                isOpen={isOpen}
                lastIdBlocks={lastIdBlocks}
                handleChangeAddBlock={handleChangeAddBlock}
              />
            ) : (
              <div>
                <ButtonViewContainer
                  setIsOpen={setIsOpen}
                  isOpen={isOpen}
                  value={title}
                />
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
}
